
  /*--------------------------------------------------------------
  # Skills
  --------------------------------------------------------------*/
  .skills {
    background-image: linear-gradient(to left, #eef7ff, #ffffff);
  }
  
  .skills .b {
    text-align: center;
  }
  
  .skills .progress {
    height: 60px;
    display: block;
    background: none;
    border-radius: 0;
  }
  
  .skills .progress .skill {
    padding: 10px 0;
    margin: 0;
    text-transform: uppercase;
    display: block;
    font-weight: 600;
    font-family: "Poppins", sans-serif;
    color: #45505b;
  }
  
  .skills .progress .skill .val {
    float: right;
    font-style: normal;
  }
  
  .skills .progressBarWrap {
    background: #f2f3f5;
  }
  
  .skills .progressBar {
    width: 1px;
    height: 10px;
    transition: 0.9s;
    background-color: #0563bb;
  }
  
 
  /*--------------------------------------------------------------
  # Portfolio
  --------------------------------------------------------------*/
  
  .portfolio .portfolioItem {
    margin-bottom: 30px;
  }
  
  .portfolio #portfolioFlters {
    padding: 0;
    margin: 0 auto 35px auto;
    list-style: none;
    text-align: center;
    background: #eef7ff;
    border-radius: 50px;
    padding: 2px 15px;
  }
  
  .portfolio #portfolioFlters li {
    cursor: pointer;
    display: inline-block;
    padding: 10px 15px 8px 15px;
    font-size: 14px;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
    color: #272829;
    margin-bottom: 5px;
    transition: all 0.3s ease-in-out;
  }
  
  .portfolio #portfolioFlters li:hover,
  .portfolio #portfolioFlters li.filterActive {
    color: #0563bb;
  }
  
  .portfolio #portfolioFlters li:last-child {
    margin-right: 0;
  }
  
  .portfolio .portfolioWrap {
    transition: 0.3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
      rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    padding: 8px;
  }
  
  .portfolio .portfolioWrap::before {
    content: "";
    background: rgba(255, 255, 255, 0.3);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: all ease-in-out 0.3s;
    z-index: 2;
    opacity: 0;
  }
  
  .portfolio .portfolioWrap .portfolio-links {
    opacity: 1;
    left: 0;
    right: 0;
    bottom: -60px;
    z-index: 3;
    position: absolute;
    transition: all ease-in-out 0.3s;
    display: flex;
    justify-content: center;
  }
  
  .portfolio .portfolioWrap .portfolio-links a {
    color: #fff;
    font-size: 28px;
    text-align: center;
    background: #0563bb;
    transition: 0.3s;
    width: 100%;
  }
  
  .portfolio .portfolioWrap .portfolio-links a:hover {
    background: #3e7fbd;
  }
  
  .portfolio .portfolioWrap .portfolio-links a + a {
    border-left: 1px solid #0563bb;
  }
  
  .portfolio .portfolioWrap:hover::before {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
  }
  
  .portfolio .portfolioWrap:hover .portfolio-links {
    opacity: 1;
    bottom: 0;
  }
  
  .portfolio .portfolioTitle {
    padding-top: 10px;
    text-align: center;
  }
  
  section {
    padding: 60px 0;
    overflow: hidden;
  }
  
  .sectionTitle {
    text-align: center;
    padding-bottom: 30px;
  }
  
  .sectionTitle h2 {
    font-size: 32px;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
    padding-bottom: 20px;
    position: relative;
    color: black;
  }
  
  .sectionTitle h2::before {
    content: "";
    position: absolute;
    display: block;
    width: 120px;
    height: 1px;
    background: #ddd;
    bottom: 1px;
    left: calc(50% - 60px);
  }
  
  .sectionTitle h2::after {
    content: "";
    position: absolute;
    display: block;
    width: 40px;
    height: 3px;
    background: #0563bb;
    bottom: 0;
    left: calc(50% - 20px);
  }
  
  .sectionTitle p {
    text-align: left;
  }
  