#header {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 9997;
    transition: all 0.5s;
    padding: 15px;
    overflow-y: auto;
  }
  
  @media (max-width: 992px) {
    #header {
      width: 300px;
      background: #fff;
      border-right: 1px solid #e6e9ec;
      left: -300px;
    }
  }
  
  @media (min-width: 992px) {
    #main {
      margin-left: 100px;
    }
  }
  .navMenu {
    padding: 0;
    display: block;
  }
  
  .navMenu * {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .navMenu > ul > li {
    position: relative;
    white-space: nowrap;
  }
  
  .navMenu a {
    display: flex;
    align-items: center;
    color: #45505b;
    padding: 10px 18px;
    margin-bottom: 8px;
    transition: 0.3s;
    font-size: 15px;
    border-radius: 50px;
    background: #f2f3f5;
    height: 56px;
    width: 100%;
    overflow: hidden;
    transition: 0.3s;
  }
  
  .navMenu a i {
    font-size: 20px;
  }
  
  .navMenu a span {
    padding: 0 5px 0 7px;
    color: #45505b;
  }
  
  @media (min-width: 992px) {
    .navMenu a {
      width: 56px;
    }
  
    .navMenu a span {
      display: none;
      color: #fff;
    }
  }
  
  .navMenu a:hover,
  .navMenu .active,
  .navMenu li:hover > a {
    color: #fff;
    background: #0563bb;
  }
  
  .navMenu a:hover span,
  .navMenu .active span,
  .navMenu li:hover > a span {
    color: #fff;
  }
  
  .navMenu a:hover,
  .navMenu li:hover > a {
    width: 100%;
    color: #fff;
  }
  
  .navMenu a:hover span,
  .navMenu li:hover > a span {
    display: block;
  }
  .mobileNavToggle{
    position: fixed;
    right: 10px;
    top: 10px;
    z-index: 9998;
    border: 0;
    background: none;
    font-size: 28px;
    transition: all 0.4s;
    outline: none !important;
    line-height: 0;
    cursor: pointer;
    border-radius: 50px;
    padding: 5px;
  }
  
  .mobileNavToggle i {
    color: #45505b;
  }
  
  .mobile-nav-active {
    overflow: hidden;
  }
  
  .mobile-nav-active #header {
    left: 0;
  }
  
  .mobile-nav-active .mobileNavToggle{
    color: #fff;
    background-color: #0563bb;
  }